import AbstractView from './../framework/view/abstract-view.js';

function createFilterItemTemplate(filter, isChecked){
  const { type } = filter;
  return (`
    <div class="trip-filters__filter">
      <input id="filter-${type}" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="${type}" ${isChecked ? 'checked' : ''}>
      <label class="trip-filters__filter-label" for="filter-${type}">${type}</label>
    </div>
  `);
}

function createFiltersTemplate(filters) {
  const filterList = filters.map((filter, i) => createFilterItemTemplate(filter, i === 0)).join('');

  return `
    <form class="trip-filters" action="#" method="get">
      ${filterList}
      <button class="visually-hidden" type="submit">Accept filter</button>
    </form>
  `;
}

export default class FiltersView extends AbstractView {
  #filters = [];

  constructor({filters}) {
    super();
    this.#filters = filters;
  }

  get template() {
    return createFiltersTemplate(this.#filters);
  }
}
